#root .wrapContent .comWrap {
  border: 1px solid #e8e8e8;
  margin: 10px 5px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  border-radius: 5px;
}
#root .wrapContent .comWrap .comLeft {
  width: 100%;
}
#root .wrapContent .comWrap .comRight {
  overflow: auto;
  width: 100%;
}
#root .wrapContent .comWrap .comRight h4 {
  font-weight: 500;
  font-size: 18px;
  color: #707070;
}
#root .wrapContent .comWrap .comRight .details {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  width: 100%;
}